import {
    ADD_MULTIPLE_CHOICE_ANSWERS_ELEMENT, ADD_QUESTIONING_ANSWERS_ELEMENT,
    ADD_SINGLE_CHOICE_ANSWERS_ELEMENT, ADD_TASK_ANSWER_ELEMENT, ADD_TEXT_ANSWER_ELEMENT, ADD_VOTING_ANSWERS_ELEMENT,
    ADD_YES_NO_ANSWERS_ELEMENT, SET_SLIDE_TIME_ACTION
} from "@/store/actions/action-types";
import {SET_SLIDE_PRICE} from "@/store/mutations/mutation-types";

export default {
    YesNo: {
        initial: {
            actions: [
                {name: ADD_YES_NO_ANSWERS_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 1},
            ],
        }
    },
    SingleChoice: {
        initial: {
            actions: [
                {name: ADD_SINGLE_CHOICE_ANSWERS_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 1},
            ],
        }
    },
    MultipleChoice: {
        initial: {
            actions: [
                {name: ADD_MULTIPLE_CHOICE_ANSWERS_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 1},
            ],
        }
    },
    TextAnswer: {
        initial: {
            actions: [
                {name: ADD_TEXT_ANSWER_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 1},
            ],
        }
    },
    Questioning: {
        initial: {
            actions: [
                {name: ADD_QUESTIONING_ANSWERS_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 0},
            ],
        }
    },
    Voting: {
        initial: {
            actions: [
                {name: ADD_VOTING_ANSWERS_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 0},
            ],
        }
    },
    Task: {
        initial: {
            actions: [
                {name: ADD_TASK_ANSWER_ELEMENT},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 1},
            ],
        }
    },
    Info: {
        initial: {
            actions: [
                {name: SET_SLIDE_TIME_ACTION, payload: 0},
            ],
            mutations: [
                {name: SET_SLIDE_PRICE, payload: 0},
            ],
        }
    },
}