import {
    ADD_SLIDE_ACTION,
    DUPLICATE_SLIDE_ACTION,
    DELETE_SLIDE_ACTION,
    UPPER_SLIDE_ACTION, LOWER_SLIDE_ACTION, SET_SLIDE_TIME_ACTION, SET_SLIDE_TYPE_ACTION
} from "@/store/actions/action-types";
import {
    ADD_SLIDE,
    DELETE_SLIDE,
    INSERT_SLIDE,
    LOWER_SLIDE,
    SET_ACTIVE_SLIDE,
    UPPER_SLIDE
} from "@/store/mutations/mutation-types";

let slideIdCounter = 1
const defaultSlideType = 2
const defaultSlideTime = 60

export default {
    [ADD_SLIDE_ACTION]({state, commit, dispatch}) {
        let slideType = defaultSlideType
        if (state.slides.length > 0) {
            slideType = state.slides[state.slides.length - 1].type
        }

        new Promise((resolve) => {
            // resolve({id: slideIdCounter++, type: 1, slideElements: []})
            resolve({id: slideIdCounter++, type: -1, time: 0, price: 0, slideElements: []})
        }).then(newSlide => {
            commit(ADD_SLIDE, newSlide)
            return newSlide
        }).then(newSlide => commit(SET_ACTIVE_SLIDE, newSlide.id))
            .then(() => dispatch(SET_SLIDE_TIME_ACTION, defaultSlideTime))
            .then(() => dispatch(SET_SLIDE_TYPE_ACTION, slideType))
    },
    [UPPER_SLIDE_ACTION]({commit}, slideId) {
        new Promise((resolve) => {
            resolve(slideId)
        }).then(slideId => commit(UPPER_SLIDE, slideId))
    },
    [LOWER_SLIDE_ACTION]({commit}, slideId) {
        new Promise((resolve) => {
            resolve(slideId)
        }).then(slideId => commit(LOWER_SLIDE, slideId))
    },
    [DUPLICATE_SLIDE_ACTION]({commit, state}, slideId) {
        let slide = state.slides.find(slide => slide.id === slideId)
        let newSlide = JSON.parse(JSON.stringify(slide))
        newSlide.id += 100
        newSlide.slideElements.forEach(el => {
            el.id += 1000
            if (el.content.component === 'TimerObject') {
                el.content.value = newSlide.id
            }
        })

        new Promise((resolve) => {
            resolve(newSlide)
        }).then(newSlide => commit(INSERT_SLIDE, {newSlide, afterSlideId: slideId}))
    },
    // eslint-disable-next-line no-unused-vars
    [DELETE_SLIDE_ACTION]({state, commit}, slideId) {
        new Promise((resolve) => {
            resolve()
        }).then(() => {
            const slideIndex = state.slides.findIndex(slide => slide.id === slideId)
            if (slideIndex !== state.slides.length - 1) {
                // слайд не был последним
                commit(SET_ACTIVE_SLIDE, state.slides[slideIndex + 1].id)
            }
            commit(DELETE_SLIDE, slideId)
        })
    },
}