<template>
  <b-modal
      v-model="showModal"
      title="Вставить видео YouTube"
      cancel-title="Отмена"
      ok-title="Добавить"
      @ok="addVideo"
  >
    <div class="form-group">
      <label for="urlInput">Ссылка на ролик YouTube:</label>
      <input type="url"
             class="form-control"
             id="urlInput"
             v-model="url"
             placeholder="https://www.youtube.com/watch?v=???"
      >
    </div>
  </b-modal>
</template>

<script>
export default {
  name: 'linkModal',
  props: {},
  data: function () {
    return {
      showModal: false,
      url: '',
    }
  },
  computed: {},
  watch: {},
  methods: {
    toggle() {
      this.url = ''
      this.showModal = true
    },

    addVideo() {
      let result = this.url.match(/https:\/\/www\.youtube\.com\/watch\?v=(.+)/);
      if (result !== null) {
        this.$emit('set-video-id', result[1])
        this.showModal = false
      } else {
        alert('Неверный формат URL')
        this.url = ''
      }
    },
  },
}
</script>

